
/* products */
.products{
        text-align: center;
        margin-top: 100px;
        margin-bottom: 100px;
}
.big-container
{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-top: 80px;

}
.big-container .container
{
      width: 23%;
      border: 1px solid #cce7d0;
      margin-bottom: 20px;
      border-radius: 10%;
      
      
} 
.big-container .container:hover{
        box-shadow: 2px 15px 15px rgba(0,0,0,0.1);
}
.products img
{      padding: 20px;
        width: 100px;
        border-radius: 15%;
        padding-bottom:4px ;
        
} 
.products>h1{
        font-weight: 700;
        font-size: 50px;
        line-height: 80px;
}
.big-container .container .information{
text-align: left;
padding-left: 20px;
 margin-bottom: 10px;
}
.big-container .container .information>h3
{
        color:green;
}